.blocker {
  z-index: 999 !important; }
.pop-up {
  .close-modal {
    display: none !important; }
  &__close {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 20px;
    top: 20px;
    span {
      &:before, &:after {
        content: '';
        width: 15px;
        height: 2px;
        background: $pinkish-grey;
        position: absolute;
        top: 15px;
        right: 8px;
        @include tr; }
      &:before {
        transform: rotate(45deg); }
      &:after {
        transform: rotate(-45deg); } }
    &:hover {
      span:before, span:after {
        background: $pink-red; } } }
  &__pay.modal {
    max-width: 665px;
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.19);
    background-color: $white;
    padding: 0;
    overflow: hidden;
    h2 {
      padding: 30px;
      @include center-y;
      font-size: 25px;
      font-weight: 300;
      color: $dark-two;
      @include r(767) {
        font-size: 20px;
        padding-right: 50px; }
      img {
        margin-right: 15px; } }
    table {
      th {
        font-size: 12px;
        font-weight: bold;
        text-align: center;
        color: $dark-two;
        text-transform: uppercase;
        padding: 20px 10px;
        width: 33%;
        @include r(550) {
          font-size: 10px; } }
      td {
        font-size: 14px;
        font-weight: normal;
        color: $brownish-grey-two;
        padding: 20px 30px;
        line-height: 1.3;
        border: 1px solid $pinkish-grey;
        width: 33%;
        @include r(550) {
          font-size: 12px;
          padding: 20px 10px; }
        b, a {
          color: $dark-two;
          font-weight: 600; }
        a {
          color: $pink-red;
          &:hover {
            color: $dark-two; } } } } } }
